<template>
    <div id="article">
        <Bar title="所有文章"></Bar>
        <el-table :data="pageData" border style="width: 100%">
          <el-table-column label="ID" width="100">
              <template scope="scope">
                  <span>{{ scope.row.id }}</span>
              </template>
          </el-table-column>
          <el-table-column label="作者" width="200">
              <template scope="scope">
                  <span>{{ scope.row.nickname }}</span>
              </template>
          </el-table-column>
          <el-table-column label="标题" width="300">
              <template scope="scope">
                  <span>{{ scope.row.title }}</span>
              </template>
          </el-table-column>
          <el-table-column label="时间" width="300">
              <template scope="scope">
                  <el-icon name="time"></el-icon>
                  <span>{{ time(scope.row.time) }}</span>
              </template>
          </el-table-column>
          <el-table-column label="操作">
              <template scope="scope">
                  <el-button size="small" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
              </template>
          </el-table-column>
        </el-table>
        <el-pagination
          layout="total, prev, pager, next"
          @current-change="getArticle"
          :page-size="100"
          :total="articleCount">
        </el-pagination>
    </div>
</template>

<script>
import Bar from './Bar.vue'

export default {
    name: 'Article',
    data () {
        return {
            pageData: [],
            allArticle: {},
        };
    },
    computed: {
        articleCount: function () { return this.$store.counts.article; }
    },
    components: { Bar },
    mounted () {
        this.getArticle(1);
    },
    methods: {
        getArticle (pageId) {
            if (this.allArticle[pageId]) {
                this.pageData = this.allArticle[pageId];
                return;
            }
            this.$axios.get('http://127.0.0.1:3000/admin/getart/' + (pageId - 1))
                .then((res) => {
                    this.pageData = res.data.result;
                    this.allArticle[pageId] = res.data.result;
                })
                .catch((res) => { console.log(res); });
        },
        time (val) {
            return new Date(val).toLocaleString();
        },
        del (index, row) {
            this.$confirm('确定要永久删除该文章吗', '提示', {
                confirmButtonText: '确认删除',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.get('http://127.0.0.1:3000/admin/delarticle/' + row.id)
                    .then(({data}) => {
                        if (data.result) {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.pageData.splice(index, 1);
                            this.$store.updateCount('article');
                        }
                    }).catch((res) => { console.log(res); });
            }).catch(() => {});
        },
    },
}
</script>

<style>

</style>






